html {
  color: #212121;
  background-color: #fff;
  font-family: verdana, arial, sans-serif;
  font-size: 22px;
  line-height: 1.3;
}

turbo-frame {
  display: block;
}

input, textarea, select, button {
  font-size: 100%;
}

a {
  color: #ec005a;
}

blockquote {
  border-left: 4px solid #ccc;
  margin-left: 10px;
  padding: 10px 20px;
}

.turbo-progress-bar {
  background-color: #ec005a;
}

.submit-button {
  color: #fff;
  cursor: pointer;
  background-color: #ec005a;
  border: none;
  border-radius: 4px;
  padding: 10px 23px;
}

.submit-button[disabled] {
  cursor: wait;
  background-color: #212121;
}

